<!-- eslint-disable max-len -->
<template>
  <div class="card-body">
    <div
      class="modal fade"
      ref="staticBackdrop"
      data-bs-backdrop="static"
      data-bs-keyboard="false"
      tabindex="-1"
      aria-labelledby="staticBackdropLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered modal-xl">
        <div class="modal-content rounded-0 border-2 border-dark">
          <div class="modal-header paperDarkBg border-bottom border-dark">
            <h1 class="modal-title fs-5" id="staticBackdropLabel">
              ADD NEW Order
            </h1>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body p-0 paperDarkBg">
            <div class="row">
              <div class="col-7">
                  <Chartlist
                    :cookieData="cartList"
                    :productList="productList"
                  />
              </div>
              <div class="col-5">
                  <Options :productList="productList" @addToCart="addToCart" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie';
import { apiGetProducts } from '@/assets/javascript/api';
import Options from '@/components/shop/ShoppingOptionsModel.vue';
import Chartlist from '@/components/shop/CartListModel.vue';
import { Modal } from 'bootstrap';

export default {
  data() {
    return {
      staticBackdrop: '',
      cartList: [],
      productList: [],
      tempCookie: '',
    };
  },
  components: {
    Options,
    Chartlist,
  },
  methods: {
    forceRerender() {
      this.componentKey += 1;
    },
    getProducts() {
      this.$emitter.emit('loadingStatus', true);
      apiGetProducts()
        .then((res) => {
          this.productList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
      this.$emitter.emit('loadingStatus', false);
    },
    addToCart(product) {
      const optionExist = this.cartList.findIndex(
        (element) => element.optionId === product.optionId
          && element.ice === product.ice
          && element.sugar === product.sugar
          && element.size === product.size,
      );
      if (Cookies.get('LULUchartList') === undefined) {
        Cookies.set(
          'LULUchartList',
          JSON.stringify({
            productId: product.productId,
            optionId: product.optionId,
            ice: product.ice,
            sugar: product.sugar,
            quantity: product.quantity,
            flavor: product.flavor,
            size: product.size,
            price: product.price,
            type: product.type,
            name: product.flavor.split('.')[1],
          }),
          {
            expires: 7,
          },
        );
      } else if (optionExist > -1) {
        this.tempCookie = '';
        this.cartList[optionExist].quantity += product.quantity;
        this.cartList.forEach((item) => {
          this.tempCookie += `${JSON.stringify(item)};`;
        });
        this.tempCookie = this.tempCookie.replace(/.$/, '');
        Cookies.set('LULUchartList', this.tempCookie, {
          expires: 7,
        });
      } else {
        let currentItem = Cookies.get('LULUchartList');
        currentItem = `${currentItem}; ${JSON.stringify({
          productId: product.productId,
          optionId: product.optionId,
          ice: product.ice,
          sugar: product.sugar,
          quantity: product.quantity,
          flavor: product.flavor,
          size: product.size,
          price: product.price,
          type: product.type,
          name: product.flavor.split('.')[1],
        })}`;
        Cookies.set('LULUchartList', currentItem, {
          expires: 7,
        });
      }
      this.getCookie();
    },
    toggleModal() {
      this.staticBackdrop.toggle();
    },
    getCookie() {
      this.cartList = [];
      if (Cookies.get('LULUchartList')) {
        const tempCookie = Cookies.get('LULUchartList').split(';');
        Object.values(tempCookie).forEach((item) => {
          this.cartList.push(JSON.parse(item));
        });
      }
    },
  },
  mounted() {
    this.getProducts();
    this.getCookie();
    this.$emitter.on('getCookie', () => {
      this.getCookie();
    });
    this.staticBackdrop = new Modal(this.$refs.staticBackdrop);
    this.$emitter.on('toggleModal', () => {
      this.getCookie();
      this.toggleModal();
    });
  },
};
</script>
