<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <section class="row overflow-scroll">
    <AddEditModal ref="addEdit" />
    <InfoModal ref="infoModal"/>
    <div class="card border-dark sticky-top w-100 paperDarkBg">
      <div class="card-body d-flex align-items-start justify-content-between">
        <div class="h5 me-3 mt-auto d-flex align-items-center mb-0">
          <small class="text-secondary">2025/01/01</small>
          <i class="bi bi-caret-right-fill h6 mb-0 mx-2"></i>
          <small class="text-secondary">2025/01/01</small>,
          <small class="ms-3 text-secondary">today</small>
          <i class="bi bi-caret-down-fill h6 mb-0"></i>
        </div>
        <div>
          <button
            type="button"
            class="btn btn-primary"
            @click="this.$refs.addEdit.toggleModal()"
          >
            Ordering at the counter
          </button>
        </div>
      </div>
    </div>
    <div class="card border-dark">
      <div class="card-body">
        <table class="table align-middle table-hover">
          <thead>
            <tr>
              <th scope="col" style="width: 25%">Customer</th>
              <th scope="col" style="width: 25%">Product List</th>
              <th scope="col" style="width: 10%">Total</th>
              <th scope="col" style="width: 15%">Add to Cart<br>Check-out</th>
              <th scope="col" style="width: 15%">View</th>
              <th scope="col" style="width: 10%">Status</th>
            </tr>
          </thead>
          <tbody>
            <template v-for="item in orderList" :key="item._id">
              <tr>
                <th>
                  <div class="d-flex align-items-center">
                    {{ item._id }}
                  </div>
                </th>
                <td>
                  <ol class="test">
                    <template
                      v-for="(data, key) in item.productList"
                      :key="key"
                    >
                      <li class="py-3">
                        <span class="fst-italic fw-bolder">{{
                          $t(data.flavor)
                        }}</span
                        >-<span class="text-secondary fw-bolder">{{
                          data.quantity
                        }}</span>
                        <div
                          v-if="data.type === 'drink' || data.type === 'coffee'"
                        >
                          ice:<span class="text-secondary fw-bolder">{{
                            $t(iceList[data.ice].title)
                          }}</span
                          >,sugar:<span class="text-secondary fw-bolder">{{
                            $t(iceList[data.sugar].title)
                          }}</span>
                        </div>
                      </li>
                    </template>
                  </ol>
                </td>
                <td>${{ $toCurrency(item.amount) }}</td>
                <td>
                  {{ item.createTime.split('T')[0] }}
                  {{ item.createTime.split('T')[1].replace(/\.\d+Z$/, '') }}
                </td>
                <td>
                  <a href="#" @click.prevent="this.$refs.infoModal.getOrder(item._id)"
                    ><i class="bi bi-pencil"></i
                  ></a>
                </td>
                <td>
                  <select
                    class="form-select text-light"
                    :class="{
                      'bg-warn': item.mealStatus === 25,
                      'bg-info': item.mealStatus === 50,
                      'bg-gary': item.mealStatus === 100,
                    }"
                    aria-label="Default select example"
                    :value="item.mealStatus"
                    @change="updateMealStatus(item._id, $event)"
                  >
                    <option value="25">UNPAID</option>
                    <option value="50">PAID</option>
                    <option value="100">DONE</option>
                  </select>
                </td>
              </tr>
            </template>
          </tbody>
        </table>
      </div>
    </div>
  </section>
</template>

<style lang="scss" scoped>
.test li {
  border-bottom: 1px dashed #333;
}
.test li:last-child {
  border: 0px;
}
</style>

<script>
import iceOption from '@/assets/json/iceOption.json';
import sugarOption from '@/assets/json/sugarOption.json';
import AddEditModal from '@/components/dashboard/order/AddEditModel.vue';
import InfoModal from '@/components/dashboard/order/InfoModel.vue';
import {
  apiAdminGetOrders,
  apiAdminUpdateOrders,
} from '@/assets/javascript/api';
import { getToken } from '@/assets/javascript/methods';

export default {
  data() {
    return {
      iceList: iceOption,
      sugarList: sugarOption,
      orderList: [],
    };
  },
  components: {
    AddEditModal,
    InfoModal,
  },
  methods: {
    getOrders() {
      this.$emitter.emit('loadingStatus', true);
      apiAdminGetOrders()
        .then((res) => {
          this.orderList = res.data.data;
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    checkSignIn() {
      // 取得 cookie 中的 token
      const token = getToken();
      // 若有 token 則登入，反之轉為登入頁面
      if (token) {
        this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
      } else {
        this.$router.push('/backend');
      }
    },
    updateMealStatus(id, e) {
      this.$emitter.emit('loadingStatus', true);
      apiAdminUpdateOrders(id, { mealStatus: e.target.value })
        .then(() => {
          this.getOrders();
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
  },
  mounted() {
    this.getOrders();
    this.$emitter.on('getAdminOrders', () => {
      this.getOrders();
    });
  },
  created() {
    this.checkSignIn();
  },
};
</script>
