<template>
  <div
    class="modal fade"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="staticBackdrop"
  >
    <div class="modal-dialog modal-dialog-centered modal-xl">
      <div class="modal-content">
        <div class="modal-header paperDarkBg">
          <h1 class="modal-title fs-5" id="exampleModalLabel">Modal title</h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="card border-0">
            <div class="card-body">
              <h5
                class="card-title mb-2 text-body-secondary d-flex justify-content-between"
              >
                <div>
                  {{ $t('orderView.orderStatus') }}：<span
                    class="fst-italic text-secondary"
                    v-if="tempOrder.mealStatus === 25"
                  >
                    {{ $t('orderView.payment') }}</span
                  >
                  <span
                    class="fst-italic text-secondary"
                    v-else-if="tempOrder.mealStatus === 50"
                    >{{ $t('orderView.diningOut') }}</span
                  >
                  <span class="fst-italic text-secondary" v-else>{{
                    $t('orderView.finish')
                  }}</span>
                </div>
              </h5>
              <h5 class="mb-2">
                {{ $t('shoppingOptions.paymethod') }}：<span
                  class="fst-italic text-secondary"
                  >{{ $t(tempOrder.payMethod) }}</span
                >
              </h5>
              <h5>
                {{ $t('orderView.total') }}：<span
                  class="fst-italic text-secondary"
                  >${{ $toCurrency(tempOrder.amount) }}</span
                >
              </h5>
              <a href="#" class="card-link">Card link</a>
              <a href="#" class="card-link">Another link</a>
            </div>
          </div>
          <table class="table table-bordered border-primary m-0 table-striped">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">
                  {{ $t('shoppingOptions.product') }}
                </th>
                <th scope="col">
                  {{ $t('shoppingOptions.price') }}
                </th>
                <th scope="col">{{ $t('shoppingOptions.quantity') }}</th>
                <th scope="col">{{ $t('shoppingOptions.sub') }}</th>
              </tr>
            </thead>
            <tbody class="table-group-divider luluText">
              <template
                v-for="(item, index) in tempOrder.productList"
                :key="index"
              >
                <tr>
                  <td>{{ index + 1 }}</td>
                  <td>
                    <div class="fw-bolder">{{ $t(item.flavor) }}</div>
                    <div
                      class="mb-0 d-md-flex"
                      v-if="item.type === 'drink' || item.type === 'coffee'"
                    >
                      <p class="text-nowrap mb-0">
                        {{ $t('shoppingOptions.size') }}:<span
                          class="fst-italic text-secondary mb-0"
                          >{{ item.size }}</span
                        >
                      </p>
                      <p class="text-nowrap mb-0 ms-sm-3">
                        {{ $t('shoppingOptions.drink.ice') }}:<span
                          class="fst-italic text-secondary mb-0"
                          >{{ $t(iceList[item.ice].title) }}</span
                        >
                      </p>
                      <p class="text-nowrap ms-sm-3 mb-0">
                        {{ $t('shoppingOptions.drink.sugar') }}:<span
                          class="fst-italic text-secondary"
                          >{{ $t(sugarList[item.sugar].title) }}</span
                        >
                      </p>
                    </div>
                  </td>
                  <td>${{ item.price }}</td>
                  <td>
                    <nav aria-label="...">
                      <ul class="pagination mb-0 align-items-center">
                        <li class="page-item">
                          <a
                            class="page-link rounded-0"
                            href="#"
                            @click.prevent="changeQty(index, -1)"
                          >
                            -
                          </a>
                        </li>
                        <li class="">
                          <div class="mx-2">{{ item.quantity }}</div>
                        </li>
                        <li class="page-item">
                          <a
                            class="page-link rounded-0"
                            href="#"
                            @click.prevent="changeQty(index, 1)"
                          >
                            <i class="bi bi-plus-lg"></i>
                          </a>
                        </li>
                      </ul>
                    </nav>
                  </td>
                  <td>
                    $<span class="fw-bold text-secondary">{{
                      item.price * item.quantity
                    }}</span>
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
        <div class="modal-footer paperDarkBg d-flex justify-content-between">
          <button
            type="button"
            class="btn btn-outline-danger"
            data-bs-dismiss="modal"
            v-if="tempOrder.mealStatus === 25"
            @click="deleteOrder"
          >
            delete order
          </button>
          <div></div>
          <button type="button" class="btn btn-primary">Save changes</button>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.circle-2 {
  width: 6rem;
}
</style>

<script>
import { apiGetOrder, apiDeleteOrder } from '@/assets/javascript/api';
import iceOption from '@/assets/json/iceOption.json';
import sugarOption from '@/assets/json/sugarOption.json';
import { Modal } from 'bootstrap';
// import CircleTwo from '../../helpers/focus/CircleTwo.vue';

export default {
  data() {
    return {
      staticBackdrop: '',
      tempOrder: {
        mealStatus: 0,
        payMethod: 'shoppingOptions.paymethods.cash',
        amount: 0,
      },
      iceList: iceOption,
      sugarList: sugarOption,
    };
  },
  methods: {
    getOrder(id) {
      apiGetOrder(id)
        .then((res) => {
          this.tempOrder = res.data.data;
          this.toggleModal();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    toggleModal() {
      this.staticBackdrop.toggle();
    },
    deleteOrder() {
      this.$emitter.emit('loadingStatus', true);
      // eslint-disable-next-line no-underscore-dangle
      apiDeleteOrder(this.tempOrder._id)
        .then(() => {
          this.$emitter.emit('getAdminOrders');
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
  },
  mounted() {
    this.staticBackdrop = new Modal(this.$refs.staticBackdrop);
  },
};
</script>
